import { Component, OnInit, ViewChild } from '@angular/core';
import { JwtService } from '../../../../shared-ui/jwt.service';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../../shared-ui/global.service';
import { ToastrService } from 'ngx-toastr';
import { currentUser } from '../../../../layouts/home-layout/user.model';
import { User, CompanySearch } from '../../../../shared-ui/models/global.model';
import { ModalDirective } from 'ngx-bootstrap';
import { AlertService } from '../../../../shared-ui/alert/alert.service';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-useradd',
  templateUrl: './useradd.component.html',
  styleUrls: ['./useradd.component.scss']
})
export class UseraddComponent implements OnInit {
  @ViewChild("addGroupModal", { static: false })
  public addGroupModal: ModalDirective;
  permission = {
    view: false,
    edit: false,
    delete: false
  }
  currentUser: currentUser = new currentUser;
  companySearch: CompanySearch = new CompanySearch;
  user: User = new User;
  selectedGroups: any = [];
  addedGroup: String = "";
  companies: any;
  userID: any;
  isShDomain: any;
  loadingData: boolean = false; 

  constructor(
    private jwtService: JwtService,
    private route: ActivatedRoute,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
    private toastr: ToastrService,
    private alertService: AlertService,
    private router: Router
  ) {
    this.globalService.setLoadingLabel('Loading... Please Wait.');
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    this.companySearch.user = this.currentUser.id;
    this.route.params.subscribe((res) => {
      if(res.id){
        this.userID = res.id 
      }
    });
    if(this.currentUser['isSuccessHackerAdmin']){
      this.permission = {
        view: true,
        edit: true,
        delete: true
      }
    }
  }

  ngOnInit() {
    this.getCompanies();
  }

  getCompanies(){
    if(!this.currentUser['isSuccessHackerAdmin']){
      this.user.company = this.currentUser['company'];
    }else{
      this.spinner.show();
      this.loadingData = true;
      this.globalService.getCompanies({user: this.companySearch.user}).subscribe(data => {
        this.spinner.hide();
        if(Object.keys(data).length){
          this.companies = data.items;
          if(this.userID){
            this.getUser(this.userID);
          }else{
            this.loadingData = false;
          }
        }
      }, error => {
        this.spinner.hide();
        this.loadingData = false;
        this.toastr.error(error.error.message, 'Error '+error.status );
      });
    }
  }

  getUser(id: any){
    this.spinner.show();
    let postData = {
      user: this.currentUser.id,
      company: this.currentUser['company'].companyId,
      id: id
    }
    this.globalService.getUserById(postData).subscribe(data => {
      if(Object.keys(data).length){
        this.user = data;
        let found = this.companies.filter(e => e.companyId == this.user.company.companyId);
        if(found.length){
          this.user.company = found[0];
        }
      }
      this.spinner.hide();
      this.loadingData = false;
    }, error => {
      this.spinner.hide();
      this.loadingData = false;
      this.toastr.error(error.error.message, 'Error '+error.status );
    });
  }

  saveUser(){
    this.alertService.clear();
    if(!this.user.firstName){
      this.alertService.error('*Please fill all mandatory fields!');
    }
    this.globalService.setLoadingLabel("Inserting... Please Wait.");
    this.spinner.show();
    let postData = {
      firstName: this.user.firstName,
      lastName: this.user.lastName,
      emailAddress: this.user.emailAddress,
      isActive: this.user.isActive ? 1 : 0,
      customerAdmin: this.user.isCustomerAdmin ? 1 : 0,
      successHackerAdmin: this.user.isSuccessHackerAdmin ? 1 : 0,
      groups: this.user.company.groups,
      company: this.user.company.companyId,
      creator: this.currentUser.id
    }
    this.globalService.saveUser(postData).subscribe(data => {
      this.spinner.hide();
      if(data && data.userId){
        this.toastr.success("Data saved successfully.", "Success");
        this.router.navigate(['/user/list']);
      }else{
        this.toastr.error("There are some error while inserting the data!", "Error");
      }
    }, error => {
      this.spinner.hide();
      this.toastr.error(error.error.message, 'Error '+error.status );
    });
  }

  updateUser(){
    let postData = {
      firstName: this.user.firstName,
      lastName: this.user.lastName,
      emailAddress: this.user.emailAddress,
      isActive: this.user.isActive ? 1 : 0,
      customerAdmin: this.user.isCustomerAdmin ? 1 : 0,
      successHackerAdmin: this.user.isSuccessHackerAdmin ? 1 : 0,
      groups: this.user.company.groups,
      company: this.user.company.companyId,
      modifier: this.currentUser.id  
    }
    this.globalService.setLoadingLabel('Updating... Please Wait.');
    this.spinner.show();
    this.globalService.updateUser(postData, this.user['userId']).subscribe(data => {
      if(data && data.modifiedBy){
        this.toastr.success( "Data updated successfully.", "Success");
        this.router.navigate(['/user/list']);
      }else{
        this.toastr.error("There are some error while updating the data!", "Error");
      }
      this.spinner.hide();
    }, error => {
      this.spinner.hide();
      this.toastr.error(error.error.message, 'Error '+error.status );
    });
  }

  selectGroup(group){
    let found = this.selectedGroups.filter(e => e == group);
    if(found.length){
      this.selectedGroups.splice(this.selectedGroups.indexOf(found[0]), 1);
    }else{
      this.selectedGroups.push(group);
    }
  }

  showAddGroupModal(){
    this.addedGroup = "";
    this.addGroupModal.show();
  }
  
  addGroup(){
    this.user.company.groups.push(this.addedGroup);
    this.addGroupModal.hide();
  }

  deleteGroup() {
    if(confirm("Are you sure to delete selected groups")) {
      this.selectedGroups.forEach(element => {
        this.user.company.groups.splice(this.user.company.groups.indexOf(element), 1);
        this.selectedGroups.splice(this.selectedGroups.indexOf(element), 1);
      });
    }
  }

  changeCompany(previousCompany, company, companysEl: HTMLSelectElement): void {
    // If we're changing company from "All" to any, it's OK
    if (previousCompany === "") {
      this.user.company = company;
      return;
    }
    // Otherwise we want the user to confirm that change
    if (confirm(`Are you sure you want to change ${this.user.firstName}'s company from ${previousCompany.companyName} to ${company.companyName}? This change will apply all consumption to ${company.companyName}.`)) {
      this.user.company = company;
    } else {
      companysEl.selectedIndex = this.companies.indexOf(previousCompany) + 1;
    }
  }

  validEmail: Boolean = true;
  checkValidEmail(email?: string) {
    this.validEmail = this.globalService.ValidateEmail(email);
    let domain = email.split('@');
    this.isShDomain = (this.currentUser['config']['successhacker-domains'].indexOf(domain[1]) > -1);
    if(this.isShDomain){
      this.user.isCustomerAdmin = 0;
    }else{
      this.user.isSuccessHackerAdmin = 0;
    }
    return this.validEmail;
  }

}
